export default {
	data() {
		return {
			title: 'get/setClipboardData',
			data: ''
		}
	},
	methods: {
		dataChange: function (e) {
			this.data = e.target.value
		},
		getClipboard: function () {
			uni.getClipboardData({
				success: (res) => {
					console.log(res.data);
					const content = res.data ? '剪贴板内容为:' + res.data : '剪贴板暂无内容';
					uni.showModal({
						content,
						title: '读取剪贴板',
						showCancel: false
					})
				},
				fail: () => {
					uni.showModal({
						content: '读取剪贴板失败!',
						showCancel: false
					})
				}
			});
		},
		setClipboard: function () {
			var data = this.data;
			if (data.length === 0) {
				uni.showModal({
					title: '设置剪贴板失败',
					content: '内容不能为空',
					showCancel: false
				})
			} else {
				uni.setClipboardData({
					data: data,
					success: () => {
						// 成功处理







					},
					fail: () => {
						// 失败处理







					}
				});
			}
		}
	}
}
